    box-shadow: none;

    .check-button {
        will-change: transform, filter;
        transition: transform 200ms, filter 200ms;
    }
}

.setting:hover {
    transform: scale(102%);
    filter: brightness(120%);
    transition: transform 70ms, filter 70ms;
}
.setting:active {
    transform: scale(98%);
    filter: brightness(120%);
    transition: transform 70ms, filter 70ms;
}

.setting label {
    text-align: center;
    display: block;
    border-radius: inherit;
