input[type="checkbox"] {
    position: absolute;
    opacity: 0;
    height: 0;
    width: 0;
}

.check-button,
.number-input-block {
    box-shadow: var(--unchecked-shadow);
}

.setting:has(:checked) .check-button {
    box-shadow: none;
}

.check-button {
    border-radius: inherit;
    position: relative;
    z-index: 0;
}

.check-button::before,
.check-button::after {
    content: "";
    position: absolute;
    inset: -0.1rem;
    z-index: -1;
    border-radius: 0.6rem;
    background: conic-gradient(
        from var(--gradient-angle),
        rgb(15, 215, 215),
        rgb(148, 166, 238),
        rgb(3, 232, 232),
        rgb(208, 140, 233),
        rgb(135, 185, 235),
        rgb(15, 215, 215)
    );

    filter: opacity(0%);
    transition: filter 130ms;
}

.check-button::after {
    filter: blur(0.5rem) opacity(0%);
}

:checked + .check-button {
    background-color: var(--color-bkg-1);
}

:checked + .check-button::before,
:checked + .check-button::after {
    animation: rotation 10s linear infinite;
    filter: opacity(50%);
}

:checked + .check-button::after {
    filter: blur(0.5rem) opacity(50%);
}

input:focus-visible + .check-button {
    transform: scale(110%);
    filter: brightness(120%);
    transition: transform 180ms, filter 180ms;
}

.setting:has(:focus-visible) {
    z-index: 1;
}

