(--color-copy_btn-front);\n    --color-copy_btn-edge: linear-gradient(\n        to left,\n        hsl(275deg 28% 35%) 0%,\n        hsl(275deg 28% 50%) 25%,\n        hsl(275deg 28% 50%) 75%,\n        hsl(275deg 28% 35%) 100%\n    );\n\n    --color-confirm_btn-front: lab(89 -26.48 8.74);\n    --color-confirm_btn-rim: var(--shad-btn-rim-inner),\n        0 0 0 0.34rem var(--color-confirm_btn-front);\n    --color-confirm_btn-edge: linear-gradient(\n        to left,\n        hsl(150.5deg 60% 35%) 0%,\n        hsl(150.5deg 60% 50%) 4%,\n        hsl(150.5deg 60% 50%) 96%,\n        hsl(150.5deg 60% 35%) 100%\n    );\n\n    --color-splph-animals: lab(87 11.05 41.21);\n    --color-splph-clear: lab(86 -17.01 -13.61);\n    --color-splph-fantasy: lab(82 33.03 -21.64);\n    --color-splph-veg: lab(88 -28.68 27.73);\n    --color-splph-space: lab(81 -0.67 -3.63);\n}\n\n* {\n    box-sizing: border-box;\n}\n\n:root {\n    overflow-y: hidden;\n}\n\n.page-wrapper {\n    color: var(--color-text-1);\n    font-family: \"Azeret Mono\", \"Courier New\", Courier, monospace;\n    font-weight: normal;\n    background-color: var(--color-bkg-1);\n    padding: 15px;\n    min-height: 100vh; /* legacy browser support */\n    min-height: 100svh;\n    display: grid;\n    place-content: center;\n    justify-items: center;\n}\n\n.app-container {\n    background-color: var(--color-bkg-2);\n    border-radius: 8px;\n\n    padding: 30px 0.8rem 1.8rem;\n    /* width: 42rem; */\n    width: 52rem;\n    width: 50rem;\n    width: 47.8rem;\n\n    position: relative;\n}\n\n.title {\n    font-weight: 700;\n    margin: 5px 0 5px;\n    text-align: center;\n}\n\n.result-section {\n    background-color: var(--color-bkg-2);\n    padding: 20px 0;\n    position: relative;\n    width: 100%;\n    font-family: \"Fira code\", Courier, monospace;\n}\n\n.result-container {\n    color: white;\n    font-size: 46px;\n    letter-spacing: 1px;\n\n    height: 60px;\n    padding: 12px 2rem 0px;\n    margin: 0;\n\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: flex-start;\n    align-items: center;\n    position: relative;\n}\n\n#result {\n    display: inline-block;\n\n    color: var(--color-text-1);\n    font-size: 2rem;\n    letter-spacing: 0.25ch;\n\n    height: 1.5em;\n    min-width: 1ch;\n    border-bottom: 4px solid var(--color-result-underline);\n    padding-bottom: 1px;\n    margin: 0 5px 0 0;\n}\n\n.output-number {\n    color: var(--color-text-number);\n}\n\n.output-symbol {\n    color: var(--color-text-symbol);\n}\n\n.spellphabet-section {\n    line-height: 200%;\n\n    min-height: 8rem;\n    width: 100%;\n    padding: 1rem 2rem 0;\n}\n\n/* || Buttons */\n\n.button-vars {\n    /* transform: translateY */\n    /* ratio: .5 */\n    --front-shift: -5px;\n    --btn-shadow-shift: 2.5px;\n\n    /* ratio: .66 */\n    --hover-front-shift: -6px;\n    --hover-btn-shadow-shift: 4px;\n\n    /* ratio: ~.9 */\n    --active-front-shift: -2.1px;\n    --active-btn-shadow-shift: 1.9px;\n\n    /* transition: */\n    --transit-btn-time: 600ms;\n\n    --transit-btn-front: filter var(--transit-btn-time),\n        transform var(--transit-btn-time) cubic-bezier(0.3, 0.7, 0.4, 1);\n    --transit-btn-edge: filter var(--transit-btn-time);\n    --transit-btn-shadow: transform var(--transit-btn-time)\n        cubic-bezier(0.3, 0.7, 0.4, 1);\n\n    --transit-hover: transform 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);\n    --transit-active: transform 34ms;\n}\n\n.buttons {\n    gap: 2rem;\n    display: grid;\n    grid-template-columns: 1fr auto 1fr;\n\n    margin: auto;\n    padding-left: 1.7rem; /* manually tuned for visual balance */\n\n    width: 45rem;\n\n    position: relative;\n}\n\n.btn-sctn {\n    position: relative;\n    display: flex;\n}\n.btn-sctn.left {\n    justify-content: right;\n}\n\n.gen {\n    --color-rim: var(--color-gen_btn-rim);\n    --color-edge: var(--color-gen_btn-edge);\n    --color-front: var(--color-gen_btn-front);\n}\n\n.pushable {\n    font-family: \"Azeret Mono\", Courier, monospace;\n    font-weight: 600;\n    display: inline-block;\n    position: relative;\n    border: none;\n    border-radius: 0.75rem;\n    background: transparent;\n    padding: 0;\n    outline-offset: 8px;\n    transition: filter 600ms;\n\n    -webkit-tap-highlight-color: transparent;\n    tap-highlight-color: transparent;\n    -webkit-user-select: none;\n    user-select: none;\n}\n\n.rim {\n    background-color: transparent;\n    box-shadow: var(--color-rim);\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    border-radius: inherit;\n\n    filter: brightness(90%);\n}\n\n.shadow {\n    background: var(--color-btn-shadow);\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    border-radius: inherit;\n\n    will-change: transform;\n    transform: translateY(var(--btn-shadow-shift));\n    transition: var(--transit-btn-shadow);\n}\n\n.edge {\n    background: var(--color-edge);\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    border-radius: inherit;\n\n    transition: var(--transit-btn-edge);\n}\n\n.front {\n    font-size: var(--font-size);\n    color: var(--color-text-btn);\n    background-color: var(--color-front);\n\n    padding: var(--pad-block) var(--pad-inline);\n    display: block;\n    position: relative;\n    border-radius: inherit;\n\n    will-change: transform;\n    transform: translateY(var(--front-shift));\n    transition: var(--transit-btn-front);\n}\n\n.spellphabet-btn {\n}\n\n[data-inclusive-menu] {\n    position: relative;\n    display: inline-block;\n}\n[data-inclusive-menu-opens],\n[data-inclusive-menu] [role^=\"menuitem\"] {\n    text-align: left;\n    border: 0;\n}\n[data-inclusive-menu] [role=\"menu\"] {\n    position: absolute;\n    left: 0;\n}\n[data-inclusive-menu] [data-inclusive-menu-from=\"right\"] {\n    left: auto;\n    right: 0;\n}\n[data-inclusive-menu] [role^=\"menuitem\"] {\n    display: block;\n    min-width: 100%;\n    white-space: nowrap;\n}\n[data-inclusive-menu] [role^=\"menuitem\"][aria-checked=\"true\"] {\n    /* border: 4px solid rgb(7, 214, 7); */\n}\n\n.btn-small {\n    --font-size: 0.75rem;\n\n    /* padding */\n    --pad-block: 0.6rem;\n    --pad-inline: 1.2rem;\n}\n.btn-large {\n    --font-size: 1.25rem;\n\n    /* padding */\n    --pad-block: 0.75rem;\n    --pad-inline: 2.625rem;\n}\n.custom-menu {\n    display: grid;\n    align-items: center;\n\n    /* padding */\n    --pad-block: 0.6rem;\n    --pad-inline: 1.2rem;\n\n    /* colors */\n    --color-rim: var(--color-select_btn-rim);\n    --color-edge: var(--color-select_btn-edge);\n    --color-front: var(--color-select_btn-front);\n\n    .front {\n        font-size: 0.75rem;\n\n        padding-block: var(--pad-block);\n        padding-inline: var(--pad-inline) calc(var(--pad-inline) + 1.1rem);\n    }\n}\n\n.dropdown-icon {\n    position: absolute;\n    right: 1.26rem;\n    bottom: 0.67rem;\n\n    transform: scale(150%);\n}\n\n/* .spell-list {\n    width: 100%;\n    padding: 0 0.7rem;\n    transform: translateY(-1rem);\n}\n\n.spellphabet-option {\n    color: white;\n    font-family: \"Azeret Mono\", Courier, monospace;\n} */\n\n/* .custom-select {\n    position: relative;\n    border-radius: 0.75rem;\n}\n\n.select-label {\n    position: absolute;\n    font-size: 0.8rem;\n    margin-bottom: 0.2rem;\n}\n\n.select-spellphabet {\n    font-family: \"Azeret Mono\", Courier, monospace;\n    font-size: 1rem;\n    padding: 1em 1.5em;\n    border: 0;\n    border-radius: inherit;\n}\n\n.custom-select-arrow {\n    position: absolute;\n    bottom: 0;\n    right: 0;\n    display: block;\n    background: red;\n    height: 3em;\n    width: 2.4em;\n    pointer-events: none;\n} */\n\n.btn-copy {\n    .front {\n        padding: 0.75rem 1.2rem;\n        background-color: var(--color-copy_btn-front);\n    }\n\n    .edge {\n        background: var(--color-copy_btn-edge);\n    }\n\n    .rim {\n        box-shadow: var(--color-copy_btn-rim);\n    }\n}\n\n.pushable:hover,\n.pushable:focus-visible {\n    .front {\n        transform: translateY(var(--hover-front-shift));\n        filter: brightness(110%);\n        transition: filter 250ms,\n            transform 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);\n    }\n\n    .edge {\n        filter: brightness(110%);\n        transition: filter 250ms;\n    }\n\n    .shadow {\n        transform: translateY(var(--hover-btn-shadow-shift));\n        transition: transform 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);\n    }\n}\n\n.pushable:active {\n    .front {\n        transform: translateY(var(--active-front-shift));\n        transition: transform 34ms;\n    }\n\n    .shadow {\n        transform: translateY(var(--active-btn-shadow-shift));\n        transition: transform 34ms;\n    }\n}\n\n.pushable:focus:not(:focus-visible) {\n    outline: none;\n}\n\n/* || Input Settings */\n\n.settings-section {\n    font-size: 0.79rem;\n    display: grid;\n    grid-template-columns: min-content;\n    gap: 0.7rem;\n    margin: 2.5rem 1.2rem 0;\n\n    --unchecked-shadow: 0 0 0.4rem 0rem hsl(0, 0%, 63%);\n}\n\n.number-input-block {\n    position: relative;\n    box-shadow: 0 0 0 4px var(--color-num-bord);\n    border-radius: 0.5rem;\n}\n\n.number-input-field {\n    -moz-appearance: textfield;\n    appearance: textfield;\n\n    font-family: \"Fira code\", \"Courier New\", Courier, monospace;\n    font-size: 1.1rem;\n    font-weight: 800;\n    color: var(--color-text-number);\n\n    border-radius: inherit;\n    border: 0;\n    padding: 3px 0px 2px 10px;\n\n    height: 100%;\n    width: 100%;\n\n    will-change: filter;\n    transition: filter 100ms;\n}\n\n/* .spinner {\n    display: none;\n    width: 0px;\n} */\n\n/* .number-input .rim-edge {\n    background: var(--color-num-rim-edge);\n    position: absolute;\n    height: 100%;\n    width: 100%;\n    border-radius: inherit;\n} */\n\ninput[type=\"number\"]::-webkit-inner-spin-button,\ninput[type=\"number\"]::-webkit-outer-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n}\n\n.length-label,\n.include-label,\n.setting {\n    /* position: relative; */\n    grid-row: 1;\n    width: fit-content;\n}\n\n.length-label,\n.include-label {\n    text-align: end;\n    display: block;\n    padding: 0.6rem 0rem 0.6rem 0rem;\n}\n\n.setting {\n    border-radius: 0.5rem;\n    box-shadow: none;\n\n    .check-button {\n        will-change: transform, filter;\n        transition: transform 200ms, filter 200ms;\n    }\n}\n\n.setting:hover {\n    transform: scale(102%);\n    filter: brightness(120%);\n    transition: transform 70ms, filter 70ms;\n}\n.setting:active {\n    transform: scale(98%);\n    filter: brightness(120%);\n    transition: transform 70ms, filter 70ms;\n}\n\n.setting label {\n    text-align: center;\n    display: block;\n    border-radius: inherit;\n}\n.label-text {\n    position: relative;\n    background-color: var(--color-bkg-1);\n    padding: 0.57rem 0.6rem;\n    border-radius: inherit;\n\n    display: block;\n    height: 100%;\n}\n\ninput[type=\"checkbox\"] {\n    position: absolute;\n    opacity: 0;\n    height: 0;\n    width: 0;\n}\n\n.check-button,\n.number-input-block {\n    box-shadow: var(--unchecked-shadow);\n}\n\n.setting:has(:checked) .check-button {\n    box-shadow: none;\n}\n\n.check-button {\n    border-radius: inherit;\n    position: relative;\n    z-index: 0;\n}\n\n.check-button::before,\n.check-button::after {\n    content: \"\";\n    position: absolute;\n    inset: -0.1rem;\n    z-index: -1;\n    border-radius: 0.6rem;\n    background: conic-gradient(\n        from var(--gradient-angle),\n        rgb(15, 215, 215),\n        rgb(148, 166, 238),\n        rgb(3, 232, 232),\n        rgb(208, 140, 233),\n        rgb(135, 185, 235),\n        rgb(15, 215, 215)\n    );\n\n    filter: opacity(0%);\n    transition: filter 130ms;\n}\n\n.check-button::after {\n    filter: blur(0.5rem) opacity(0%);\n}\n\n:checked + .check-button {\n    background-color: var(--color-bkg-1);\n}\n\n:checked + .check-button::before,\n:checked + .check-button::after {\n    animation: rotation 10s linear infinite;\n    filter: opacity(50%);\n}\n\n:checked + .check-button::after {\n    filter: blur(0.5rem) opacity(50%);\n}\n\ninput:focus-visible + .check-button {\n    transform: scale(110%);\n    filter: brightness(120%);\n    transition: transform 180ms, filter 180ms;\n}\n\n.setting:has(:focus-visible) {\n    z-index: 1;\n}\n\n/* prettier-ignore */\n@keyframes rotation {\n    0%   { --gradient-angle: 0deg; }\n    100% { --gradient-angle: 360deg; }\n}\n\n@keyframes fade-in {\n    from {\n        opacity: 0;\n    }\n}\n\n@media screen and (max-width: 400px) {\n    .result-container {\n        font-size: 14px;\n    }\n}\n\n/* Dialog */\n/**\n * 1. Make the dialog container, and its child overlay spread across\n *    the entire window.\n */\n.dialog-container,\n.dialog-overlay {\n    position: fixed; /* 1 */\n    top: 0; /* 1 */\n    right: 0; /* 1 */\n    bottom: 0; /* 1 */\n    left: 0; /* 1 */\n}\n\n/**\n  * 1. Make sure the dialog container and all its descendants sits on\n  *    top of the rest of the page.\n  * 2. Make the dialog container a flex container to easily center the\n  *    dialog.\n  */\n.dialog-container {\n    z-index: 2; /* 1 */\n    display: flex; /* 2 */\n    --animation-duration: 30ms;\n}\n\n/**\n  * 1. Make sure the dialog container and all its descendants are not\n  *    visible and not focusable when it is hidden.\n  */\n.dialog-container[aria-hidden=\"true\"] {\n    display: none; /* 1 */\n}\n\n/**\n  * 1. Make the overlay look like an overlay.\n  */\n.dialog-overlay {\n    background-color: rgb(43 46 56 / 0.8); /* 1 */\n    animation: fade-in var(--animation-duration) both;\n}\n\n/**\n  * 1. Vertically and horizontally center the dialog in the page.\n  * 2. Make sure the dialog sits on top of the overlay.\n  * 3. Make sure the dialog has an opaque background.\n  */\n.dialog-content {\n    margin: auto; /* 1 */\n    margin-bottom: 30svh;\n    margin-bottom: 40svh;\n    transform: translateY(10svh);\n\n    z-index: 2; /* 2 */\n    position: relative; /* 2 */\n    background-color: white; /* 3 */\n\n    padding: 2em 2.5em;\n    max-width: 90%;\n    width: 725px;\n    border-radius: 0.5rem;\n\n    animation: fade-in 400ms var(--animation-duration) both;\n}\n\n.dialog-title {\n    margin: 0;\n}\n\n.dialog-close {\n    position: absolute;\n    top: 0.5em;\n    right: 0.5em;\n\n    border: 0;\n    border-radius: 30%;\n    padding: 0.2em;\n    background-color: transparent;\n    font-size: 1.5em;\n    width: 1.5em;\n    height: 1.5em;\n    transition: 0.15s;\n}\n\n.dialog-close:hover {\n    background-color: rgb(50 50 0 / 0.15);\n}\n\n.dialog-main {\n    display: grid;\n    grid-template-columns: 0fr 1.7fr 0fr 3fr;\n\n    --gap: 1rem;\n    gap: var(--gap);\n\n    padding-block: 1rem;\n}\n\n.dialog-main > * {\n    grid-row: 1;\n}\n\n.spell-group {\n    display: flex;\n    flex-direction: column;\n    gap: 0.6rem;\n    /* gap: 1fr; */\n    position: relative;\n    align-items: center;\n    justify-content: center;\n\n    fill: none;\n\n    --pad-block: 0.7rem;\n    padding: var(--pad-block) 0.4rem;\n\n    --option-wrapper-pad: 0.3rem;\n\n    --color-rim: var(--color-gen_btn-rim);\n    --color-edge: var(--color-gen_btn-edge);\n    --color-front: var(--color-gen_btn-front);\n}\n\n.option-wrapper,\n.option-wrapper-leg {\n    width: 100%;\n\n    padding: var(--option-wrapper-pad);\n    margin: 0.1rem;\n\n    --opt-border-radius: 0.4rem;\n    border-radius: var(--opt-border-radius);\n    position: relative;\n    fill: white;\n}\n\n.active-legend {\n    --bg-color: grey;\n    font-style: italic;\n    font-size: 0.7rem;\n    color: var(--bg-color);\n\n    position: absolute;\n    bottom: 0;\n}\n\n/* .option-wrapper:hover {\n    background-color: lab(87 7.97 -21.01);\n    fill: lab(87 7.97 -21.01);\n} */\n\n.option-wrapper:has(:checked),\n.option-wrapper:has([aria-checked]) {\n    /* --bonus-width: 1.7rem;\n    --bonus-